<template><section>

<h1>Icon图标</h1>
<p>语义化的矢量图形，<code v-pre="">VV-UI</code> 使用开源的 Iconfont (阿里妈妈MUX倾力打造的矢量图标管理、交流平台) 作为图标库，并制作成了 <code v-pre="">icon font</code>。</p>
<h2>如何使用</h2>
<p>使用 <code v-pre="">class=&quot;icon&quot;</code> 来声明图标，具体图标的名称请 copy 相应的标签</p>
<div class="demo-block">
  <i class="iconfont icon-component fs-24"></i>
  <i class="iconfont icon-survey1 fs-24"></i>
  <i class="iconfont icon-templatedefault fs-24"></i>
  <i class="iconfont icon-office fs-24"></i>
</div>
<demo-block>
                        <div slot="desc">
<i class="iconfont icon-component fs-24"></i>
<i class="iconfont icon-survey1 fs-24"></i>
<i class="iconfont icon-templatedefault fs-24"></i>
<i class="iconfont icon-office fs-24"></i>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"iconfont icon-component fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"iconfont icon-survey1 fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"iconfont icon-templatedefault fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"iconfont icon-office fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2>图标示例</h2>
<ul class="icon-list">
  <li v-for="name in icons" :key="name">
    <span>
      <i :class="'iconfont icon-' + name"></i>
      {{'icon-' + name}}
    </span>
  </li>
</ul>
</section></template>
<style lang="less">
  .demo-icon .source > i {
    font-size: 24px;
    color: #8492a6;
    margin: 0 20px;
    font-size: 1.5em;
    vertical-align: middle;
  }

  .demo-icon .source > button {
    margin: 0 20px;
  }

  .icon-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: solid 1px #eaeefb;
    border-radius: 4px;
  }
  .icon-list li {
    float: left;
    width: 16.66%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    color: #666;
    font-size: 13px;
    transition: color .15s linear;

    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;
    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
      color: #99a9bf;
    }
    i {
      display: block;
      font-size: 32px;
